import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material';

import { PROJECTS } from '../projects-data';
import { ProjectDetailComponent } from '../project-detail/project-detail.component';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.css']
})
export class ProjectsComponent implements OnInit {

  projects = PROJECTS;
  cols: number = 3;

  constructor(private dialog: MatDialog) { }

  openProjectDetails(id: number): void {

    const focusProject = this.projects.find(project => project.id === id);

    const dialogRef = new MatDialogConfig();

    dialogRef.maxHeight = '850px';
    dialogRef.maxWidth = '1200px';
    dialogRef.autoFocus = false;

    dialogRef.data = focusProject;

    this.dialog.open(ProjectDetailComponent, dialogRef);
  }

  ngOnInit() {
    this.resize();
  }

  onResize(event) {
    this.resize();
  }

  resize(): void {
    this.cols = (window.innerWidth >= 1250) ? 3 : 2;
  }
}
